import styled from "styled-components";

const Header = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 83px;
  display: flex;
  margin: 15px 141px 0 141px;
  justify-content: space-between;
  align-items: center;
`;

export default Header;



