import styled from "styled-components";

const Notification = styled.div`
  background-color: #040134;
  color: white;
  height: 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default Notification;



